<template>
	<a-form :form="form" @submit="handleSubmit" class="register-form">
		<!-- 用户名 -->
		<a-form-item v-bind="formItemLayout">
			<span slot="label">
				用户名
			</span>
			<a-input v-decorator="['nickname',{rules: [{ required: true, message: '请填写用户名!', whitespace: true }]}]" />
		</a-form-item>

		<!-- 密码 -->
		<a-form-item v-bind="formItemLayout" label="密码">
			<a-input v-decorator="['password',{rules: [{required: true,message: '请填写密码!',},{validator: validateToNextPassword}]}]" type="password" />
		</a-form-item>

		<!-- 确认密码 -->
		<a-form-item v-bind="formItemLayout" label="确认密码">
			<a-input v-decorator="['confirm',{rules: [{ required: true,message: '请填写确认密码!',},{validator: compareToFirstPassword}]}]" type="password" @blur="handleConfirmBlur" />
		</a-form-item>

		<!-- 电话 -->
		<a-form-item v-bind="formItemLayout" label="电话">
			<a-input v-decorator="['phone',{rules: [{ required: true, message: '请填写电话号码!' }]}]" style="width: 100%">
			</a-input>
		</a-form-item>

		<!-- 邮箱 -->
		<a-form-item v-bind="formItemLayout" label="邮箱">
			<a-input v-decorator="['email',{rules: [{type: 'email',message: 'The input is not valid E-mail!',},{required: true,message: '请填写邮箱!'}]}]"/>
			<a-button type="primary" @click="APIgetCode">
				发送邮箱验证
			</a-button>
		</a-form-item>

		<!-- 验证码 -->
		<a-form-item v-bind="formItemLayout" label="验证码">
			<a-row>
				<a-col :span="8">
					<a-input v-decorator="['captcha',{ rules: [{ required: true, message: '请填写验证码!'}]}]" />
				</a-col>
			</a-row>
		</a-form-item>

		<!-- 条款 -->
		<a-form-item v-bind="tailFormItemLayout">
			<a-checkbox v-decorator="['agreement', { valuePropName: 'checked' }]">
				我已阅读
				<a href="">
					同意
				</a>
			</a-checkbox>
		</a-form-item>

		<!-- 注册 -->
		<a-form-item v-bind="tailFormItemLayout">
			<a-button type="primary" html-type="submit">
				注册
			</a-button>
		</a-form-item>
	</a-form>
</template>

<script>
	import * as api from "@/api/index.js"
	export default {
		data() {
			return {
				form: this.$form.createForm(this, {
					name: 'register'
				}),
				formItemLayout: { //左边结构
					labelCol: {
						xs: {
							span: 24
						},
						sm: {
							span: 8
						},
					},
					wrapperCol: {
						xs: {
							span: 24
						},
						sm: {
							span: 16
						},
					},
				},
				tailFormItemLayout: { //右边结构
					wrapperCol: {
						xs: {
							span: 24,
							offset: 0,
						},
						sm: {
							span: 16,
							offset: 8,
						}
					}
				},
				values:"",//获取所有的信息
				email:""
			}
		},
		methods: {
			handleSubmit(e) { //提交注册
				let _self = this
				e.preventDefault();
				this.form.validateFieldsAndScroll((err, values) => {
					if (!err) {
						_self.values = values
					}
				});
				_self.APIregister()
			},
			handleConfirmBlur(e) { //失去焦点事件
				const value = e.target.value;
				this.confirmDirty = this.confirmDirty || !!value;
			},
			compareToFirstPassword(rule, value, callback) { //输入密码
				const form = this.form;
				if (value && value !== form.getFieldValue('password')) {
					callback('两次密码不一致!');
				} else {
					callback();
				}
			},
			validateToNextPassword(rule, value, callback) { //确认密码
				const form = this.form;
				if (value && this.confirmDirty) {
					form.validateFields(['confirm'], {
						force: true
					});
				}
				callback();
			},
			async APIgetCode(){//邮箱验证
				let _self = this
				let val = _self.form.getFieldsValue()
				let res = await api.sendCode({
					"email":val.email
				})
				if(res.data.code && res.data.code === 1000){
					_self.$message.success('发送成功');
				}else{
					_self.$message.error('发送失败');
				}
			},
			async APIregister(){//注册接口
				let _self = this
				let res = await api.register({
					"name":_self.values.nickname,
					"pwd":_self.values.password,
					"email":_self.values.email,
					"phone":_self.values.phone,
					"code":_self.values.captcha,
				})
				if(res.data.code && res.data.code === 1000){
					_self.$message.success('注册成功');
					_self.$router.push("/login")
				}else{
					_self.$message.error('注册失败');
				}
			}
		}
	}
</script>

<style lang="scss">
	.register-form {
		width: 400px;
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
</style>